@import url(https://fonts.googleapis.com/css?family=Varela+Round);
@import url(https://fonts.googleapis.com/css?family=Varela+Round);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.page {
  background: linear-gradient(to right, #ffa751, #ffe259);
  min-height: calc(100vh - 6rem);
  width: calc(100% - 6rem);
  color: #444;
  text-align: left;
  padding: 3rem;
}

.page-card {
  max-width: 45rem;
  height: 40vh;
  margin: 24vh auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.title {
  margin: 0 0 2rem;
  font-size: 4rem;
  text-align: center;
  font-family: Varela Round, sans-serif;
}

.pushable {
  position: relative;
  background: transparent;
  border: none;
  padding: 0;
  cursor: pointer;
  display: block;
  width: 14rem;
  margin: 5rem auto 0;
  text-decoration: none;
  font-weight: bold;
  font-family: Varela Round, sans-serif;
  text-align: center;
}
.shadow {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 12px;
  background: hsl(0deg 0% 0% / 0.25);
  transform: translateY(2px);
}
.edge {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 12px;
  background: hsl(340deg 100% 32%);
}
.front {
  display: block;
  position: relative;
  padding: 12px 42px;
  border-radius: 12px;
  font-size: 1.25rem;
  color: white;
  background: hsl(345deg 100% 47%);
  transform: translateY(-4px);
}

.pushable:hover .front {
  transform: translateY(-6px);
}
.pushable:hover .shadow {
  transform: translateY(4px);
}
.pushable:active .front {
  transform: translateY(-2px);
}
.pushable:active .shadow {
  transform: translateY(1px);
}

.pushable:focus:not(:focus-visible) {
  outline: none;
}
/**
 * Slides Custom CSS
 */

.opacity:not(.active) {
    opacity: 0;
}

.staffs:not(.active) {
  opacity: 0.01;
}

.circle#title,
.circle {
    background: #ffeb3b;
    line-height: 1.1;
    color: black;
    padding: 10rem  5rem;
    border-radius: 4% 50% 0% 70%;
    box-shadow: rgb(191 197 202) 10px 10px, rgba(0,0,0,0.3) 30px 30px;
}

#starter {
  background: black;
  color: hsla(0, 0%, 0%, .9);
  left: 0;
  letter-spacing: 5px;
  position: absolute;
  text-align: center;
  font: normal 140px Varela Round, sans-serif;
  text-transform: uppercase;
  -webkit-animation: move linear 2000ms infinite;
          animation: move linear 2000ms infinite;
}

#starter p {
  margin-top: 14rem;
}

.impress-enabled .step .btn-block {
  color: hsla(0,0%,100%,.8);
  text-decoration: none;
  padding: 0 .4em;
  background: #3e4852;
  border-radius: .2em;
  transition: all .5s;
}

.impress-enabled .step:not(.active) {
  z-index: -1;
}

.impress-enabled .step.active {
  z-index: 9999;
}

.step h2 {
  line-height: 0.8;
}

.step h2 > strong {
  font-size: 50%;
  font-weight: normal;
}

.step.large-title h2 {
  font-size: 10rem;
  font-weight: bold;
}

